<template>
    <div class="account-container">
        <div class="account-header" v-for="account in this.accounts" :key="account._id">
            <h2>Account Details - {{ account.accountType  }}</h2>
            <div class="account-details">
                <div class="account-details-item">
                    <p>Account Number: {{ maskAccountNumber(account.accountNumber) }}</p>
                    <p>Account Type: {{ account.accountType }}</p>
                    <p>Balance: {{ account.balance }}</p>
                    <p>Account Holder: {{ account.user.username }}</p>
                    <p>IBAN: {{ account.encryptedDetails.IBAN }}</p>
                    <p>Withdrawal Limit: {{ account.limitations.withdrawalLimit }}</p>
                    <p>Transfer Limit: {{ account.limitations.transferLimit }}</p>
                    <p>Other Limitations: {{ account.limitations.otherLimitations }}</p>
                    <router-link :to="{name : 'transaction', params: {id : account._id, userId: account.userId}}">All Transactions</router-link> 
                    <hr/>
                    <button @click="mockExternalPayPalPayment(account._id)">Mock PayPal Payment</button>  
                </div>
               
            </div>
        </div>
        <button @click="backToHome">Back</button>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'MainAccount',
    emits: ['setUser'],
    props : ['id'],
    data() {
        return {
            accounts: {}
        }
    },
    mounted() {
        this.fetchAccounts();
    },
    methods: {
        backToHome() {
            this.$router.go(-1);
        },
        mockExternalPayPalPayment(accountId){
            
            const amount = +prompt('Enter the amount to recieve from PayPal');
            const response = axios.put(`/api/transaction/external/${this.id}`, {
                    "accountId": accountId, // Reference to the Account Document of the transaction 
                "amount": amount,
                "type": "external", // e.g., 'debit', 'credit'
                    "paymentMethod": "PayPal",
                    "status": "Completed",
                    "payerInfo": {
                        "payerId": "AX12345BXC123456",
                        "email": "payer@example.com",
                        "firstName": "John",
                        "lastName": "Doe",
                        "shippingAddress": {
                            "line1": "123 Main St",
                            "city": "Anytown",
                            "state": "CA",
                            "postalCode": "12345",
                            "countryCode": "US"
                        }
                    },
                    "recipientInfo": {
                        "merchantId": this.id
                    }
                });
            console.log('PayPal payment response:', response);
        },
        maskAccountNumber(accountNumber) {
            // Ensure the account number is a string
            let accountStr = accountNumber.toString();

            // Check if the account number length is greater than 3
            if (accountStr.length > 3) {
                // Replace all characters except the last three with 'X'
                return 'X'.repeat(accountStr.length - 3) + accountStr.substring(accountStr.length - 3);
            } else {
                // If the account number is less than or equal to 3, return it as is
                return accountStr;
            }
        },
        async fetchAccounts() {
            try {
                const response = await axios.get(
                    `/api/account?userId=${this.id}`
                );
                this.accounts = response.data;
            } catch (error) {
                console.log(error);
            }
        }
    }
}

</script>
