<template>
    <div class="payee-picker">
        <!-- <select v-model="selectedPayee" @change="sendPayeeToParent">
        <option  v-for="payee in payees" :key="payee._id"  :value="{userId : payee._id, username : payee.username, accountId: payee.linkedAccounts[0].accountId }">
            
            {{ payee.username }} - {{ payee.linkedAccounts[0].accountType }}
        </option>
        </select> -->
        <!-- Use a search form with possible pyees when an input is forward to searchPayee function-->
    <div>
    <input 
      type="text" 
      v-model="searchTerm" 
      @input="debouncedSearch"
      placeholder="Search payees..."
    />

    <ul v-if="searchResults.length">
      <li 
        v-for="payee in searchResults" 
        :key="payee._id" 
        @click="selectPayee(payee)"
        class="payee-item"
        :class="{ 'selected-payee': payee._id === selectedPayee._id }"
      >
        {{ payee.username }} - {{ payee.account }}
        
      </li>
    </ul>
  </div>
        

    </div>
</template>
<script>
import axios from 'axios';
import _ from 'lodash'; 

export default {

    props: {
        user: Object
    },
  
  data() {
    return {
     payees: [],
     selectedPayee: {},
      searchTerm: '',
      searchResults: [],
     
    }
  },
    mounted() {
        this.getPayees();
    },
    created() {
      this.debouncedSearch = _.debounce(this.searchPayee, 300);
    },
  methods: {
    async getPayees() {
      try {
        const response = await axios.get(
          `/api/account/fts/search`
        );
        let foundPeyees = response.data.filter(payee => payee._id !== this.user._id);
        this.searchResults = foundPeyees;
        
      } catch (error) {
        console.error('Payees failed:', error);
      }
    },
    selectPayee(payee) {
      this.selectedPayee = payee;
      this.selectedPayee.userId = payee.userId;
      this.searchTerm = payee.username;
      //this.searchResults = [];
      this.sendPayeeToParent();
    },
    async searchPayee() {
      if (this.searchTerm.length > 0) {
       const response = await axios.get(
          `/api/account/fts/search?text=${this.searchTerm}`
        );
        let foundPeyees = response.data.filter(payee => payee._id !== this.user._id);
        this.searchResults = foundPeyees;
      } else {
        this.searchResults = [];
      }
    },
    sendPayeeToParent() {
        this.$emit('selection-changed', this.selectedPayee);
    },
    },
   
};



</script>
<style scoped>
.payee-item{
  cursor: pointer;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 5px;
}

.selected-payee {
  background-color: green;
  color: white;
}
</style>